<script>
export default {
    name: 'bt-text-box',
    props: {
        value: [String, Number],
        handleChange: {
            type: Function,
            default: function() {
                return;
            }
        },
        inputType: {
            type: String,
            default: 'text'
        },
        min: {
            type: String,
        },
        max: {
            type: String,
        }
    },
    data() {
        return {
            inputVal: this.inputType === 'text' ? this.value : Number(this.value)
        };
    },
    watch: {
        inputVal(val) {
            this.$emit('input', val);
        },
        value(val) {
            this.inputVal = this.inputType === 'text' ? this.value : Number(this.value);
        }
    }
};
</script>

<template>
    <input :type="inputType"
            v-model="inputVal"
            :min="min ? min : false"
            :max="max ? max : false"
            @change="handleChange"
            @keydown.stop
    >
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';

input {
    background: $near-white;
    font-size: 12px;
    height: 30px;
    outline: none;
    border: 0px;
    width: 250px;
    border-radius: 4px;
    font-weight: 400;

    &[type='number'] {
        text-align: center;
        width: 100px;
    }
}
</style>
