<script>
export default {
    name: 'bt-settings-section',
    props: {
        hasBottomBorder: {
            type: Boolean,
            default: false
        },
        hasFlexHeight: {
            type: Boolean,
            default: false
        },
    },
    computed: {
        getBottomBorder() {
            return this.hasBottomBorder ? 'border' : null;
        },
        getFlexHeight() {
            return this.hasFlexHeight ? 'flex-height' : null;
        },
    },
};
</script>

<template>
    <div :class="['section', getBottomBorder, getFlexHeight]">
        <slot name="left"></slot>
        <slot name="right"></slot>
    </div>
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';

.section {
    height: 50px;
    background-color: $white;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;

    &.border {
        border-bottom: 1px solid $white-smoke;
    }

    &.flex-height {
        height: auto;
    }
}
</style>
