<script>
export default {
    name: 'bt-toggle',
    props: {
        checked: {
            type: Boolean,
            default: true
        },
        backgroundColor: {
            type: String,
            default: '#76b83f'
        },
        handleChange: {
            type: Function,
            default: function() {
                return;
            }
        },
        height: {
            type: String,
            default: '24px'
        },
        id: {
            type: String,
            default: 'bt-toggle'
        },
        disabled: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            checkedValue: this.checked,
        };
    },
    methods: {
        getCircleDimension() {
            return  Math.round(this.heightProps * (22.5 / 30));
        },
        generateCSS() {
            const labelSelector = `.toggle-wrapper input#${this.id} + label[${this.$options._scopeId}]`;
            const labelSelectorChecked = `.toggle-wrapper input#${this.id}:checked + label[${this.$options._scopeId}]`;

            const diameter = this.getCircleDimension();
            const outerHeight = this.heightProps;
            const outerWidth = this.heightProps * Math.round(50 / 30);
            const cushion = Math.round((outerHeight - diameter) / 2);

            const outerCSS = `${labelSelector} {
                height: ${outerHeight}px;
                width: ${outerWidth}px;
                border-radius: ${outerHeight}px;
            }`;
            const circleCSS = `${labelSelector}:after {
                height: ${diameter}px;
                width: ${diameter}px;
                border-radius: ${diameter}px;
                top: ${cushion}px;
                left: ${cushion + 1}px;
            }`;
            const circleCheckedCSS = `${labelSelectorChecked}:after {
                left: ${outerWidth - outerHeight + cushion - 1}px;
            }`;

            const styleElement = document.createElement('style');
            styleElement.type = 'text/css';
            this.$el.appendChild(styleElement);
            styleElement.innerHTML = outerCSS + circleCSS + circleCheckedCSS;
        },
        change(value, event) {
            this.$emit('change', value, event);
        },
    },
    computed: {
        styleProps() {
            return {
                'background': this.backgroundColor,
            };
        },
        heightProps() {
            return parseInt(this.height.substring(0, this.height.length - 2));
        }
    },
    watch: {
        checked(newVal) {
            this.checkedValue = newVal;
        },
        checkedValue(newVal) {
            this.handleChange(newVal);
        }
    },
    mounted() {
        this.generateCSS();
    },
};
</script>

<template>
    <div class="toggle-wrapper" @click.stop>
        <input type="checkbox"
            name="bt-toggle"
            class="checkbox"
            :id="id"
            v-model="checkedValue"
            :disabled="disabled"
            @change.stop="change(checkedValue, $event)">
        <label :for="id" :style="checkedValue && styleProps"></label>
    </div>
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';

@mixin inactiveMixin {
    content: "";
    position: absolute;
    display: block;
}

@mixin beforeAnimation {
    transition: .2s cubic-bezier(.24, 0, .5, 1);
}

@mixin afterAnimation {
    box-shadow: 0 0 0 1px hsla(0, 0%, 0%, 0.1), 0 4px 0px 0 hsla(0, 0%, 0%, .04), 0 4px 9px hsla(0, 0%, 0%, .13), 0 3px 3px hsla(0, 0%, 0%, .05);
    transition: .35s cubic-bezier(.54, 1.60, .5, 1);
}

.toggle-wrapper {
    display: flex;
    position: relative;
    input {
        &.checkbox {
            display: none; // hides checkbox
            & + label {
                position: relative;
                display: inline-block;
                user-select: none;
                transition: .4s ease;
                background: $grey-dm;
                cursor: pointer;
                &:after {
                    @include inactiveMixin;
                    @include afterAnimation;
                    background: $white;
                }
            }
            // When active
            &:checked {
                & + label:before {
                    transition: width .2s cubic-bezier(0, 0, 0, .1);
                }
            }
            // When disabled
            &:disabled {
                & + label {
                    background: $grey-ll !important;
                    cursor: default;
                }
            }
        }
    }
}
</style>