<script>
export default {
    name: 'bt-checkbox-section',
    props: {
        hasBottomBorder: {
            type: Boolean,
            default: false
        },
        value: {
            type: Boolean,
            default: false
        },
        handleChange: {
            type: Function,
            default: function() {
                return;
            }
        },
        id: {
            type: String,
            default: null
        },
    },
    data() {
        return {
            inputVal: this.value
        };
    },
    watch: {
        inputVal(val) {
            this.$emit('input', val);
            this.handleChange();
        }
    }
};
</script>

<template>
    <bt-settings-section :hasBottomBorder="hasBottomBorder">
        <div slot="left">
            <slot>Checkbox</slot>
        </div>
        <label class="outer" slot="right">
            <input type="checkbox" v-model="inputVal" :id="id">
            <div class="checkbox-indicator"></div>
        </label>
    </bt-settings-section>
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';

.outer {
    position: relative;

    input[type='checkbox'] {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 0;
        padding: 0;
    }

    input[type='checkbox']:checked ~ .checkbox-indicator:after {
        display: block;
        box-sizing: border-box;
    }

    .checkbox-indicator {
        height: 18px;
        width: 18px;
        background-color: transparent;
        border: 2px solid $grey-m;
        border-radius: 2px;
        box-sizing: border-box;
        &:after {
            content: '';
            position: absolute;
            display: none;
            left: 6px;
            top: 2px;
            width: 7px;
            height: 12px;
            border: solid $green-primary;
            border-width: 0 3px 3px 0;
            transform: rotate(45deg);
        }

        &:hover {
            cursor: pointer;
            border: 2px solid $black;
        }
    }
}
</style>
