import '@bittorrent/bt-assets'; // import glyph icons transpiled by Webpack
import Vue from 'vue';

const components = [];
const requireComponent = require.context(
    // Look for files in the current directory
    './components',
    // Do not look in subdirectories
    false,
    // Only include .vue files
    /[\w-]+\.vue$/
);
requireComponent.keys().forEach(function(fileName) {
    // Get the component config
    const componentConfig = requireComponent(fileName);
    // Remove "./*" from beginning and extension from end
    const componentName = fileName.replace(/\.\w+$/, '').replace(/.*\//, '');
    // Register component to Vue using name property, componentName as fallback
    Vue.component(
        componentConfig.default.name || componentConfig.name || componentName,
        componentConfig.default || componentConfig
    );
    components.push(componentConfig.default || componentConfig);
});

export default components;