<script>
export default {
    name: 'bt-button-text',
    props: {
        isMax: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        computedClass: function() {
            return this.isMax ? 'full' : null;
        }
    }
};
</script>

<template>
    <bt-button className="button-outline--primary" :class=computedClass  v-on="$listeners">
        <slot>Default</slot>
    </bt-button>
</template>

<style lang="scss" scoped>
@import '~@bittorrent/bt-assets/scss/colors';
.button {
    &.button-outline--primary {
        min-width: 80px;
        &.full {
            min-width: 388px;
        }
    }
}
</style>
