<script>
export default {
    name: 'bt-dropdown',
    props: {
        defaultOption: {
            type: String,
            default: 'Default Option'
        },
        options: {
            type: Object,
            default: function() {
                return {
                    'first': {
                        'nativeName': 'first value',
                        'value': 'first'
                    },
                    'second': {
                        'nativeName': 'second value',
                        'value': 'second'
                    },
                    'third': {
                        'nativeName': 'third value',
                        'value': 'third'
                    }
                };
            }
        },
        getFlagClass: {
            type: Function,
            default: function() {
                return;
            }
        },
        dropdownFlagClass: String,
        onUpdateOption: {
            type: Function,
            default: () => {return;}
        },
        valueProp: {
            type: String,
            default: null
        },
        id: {
            type: String,
            default: null
        },
    },
    data() {
        return {
            open: false,
            optionsList: this.options,
            value: this.valueProp
        };
    },
    methods: {
        handleMenuToggleClick() {
            this.open = !this.open;
            this.$emit('dropdown-toggle', this.open);
        },
        close() {
            this.open = false;
        },
        updateOption(option) {
            this.handleMenuToggleClick();
            this.value = option;
            this.onUpdateOption(option);
        }
    },
    computed: {
        dropdownNativeName() {
            if (!this.optionsList) {
                return;
            }
            const selection = this.optionsList[this.valueProp];
            return selection ? selection.nativeName : this.defaultOption;
        }
    }
};
</script>

<template>
    <div id="dropdown-container" tabindex=1 @blur="close">
        <div id="dropdown-display" class="button button-outline--primary"
            :class="{ open: open }"
            @click.stop="handleMenuToggleClick">
            <i :class="dropdownFlagClass"></i>
            <span :id="id">
                {{dropdownNativeName}}
            </span>
            <i class="chevron bt-icons bt-icons-show" :class="{ open: open }"></i>
        </div>
        <div id="dropdown-options-container" v-show="open">
            <div class="dropdown-option"
                v-for="option in optionsList"
                :key="option.value"
                @click.stop="updateOption(option.value)">
                <span>
                    <i :class="getFlagClass(option.alpha2)"></i>
                    <span class="native-name">
                        {{option.nativeName}}
                    </span>
                </span>
                <span class="name">
                    ({{option.name}})
                </span>
            </div>
        </div>
    </div>
</template>

